import { ShowsService } from './../../services/shows.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  trendingMovies: any[] = [];
  trendingTv: any[] = [];
  trendingperson: any[] = [];
  imgPrefix: string = "https://image.tmdb.org/t/p/w500"
  constructor(private ShowsService:ShowsService) {
    this.ShowsService.getTrendingShows("movie").subscribe((e:any)=>{
      this.trendingMovies = e.results.slice(2,6)
    })
    this.ShowsService.getTrendingShows("tv").subscribe((e:any)=>{
      this.trendingTv= e.results.slice(2,6)
    })
    this.ShowsService.getTrendingShows("person").subscribe((e:any)=>{
      this.trendingperson = e.results.slice(2,6)
    })
   }

  ngOnInit(): void {

  }

  getStartedBtn(){
    document.querySelector('#trendingMovies')?.scrollIntoView()
  }

}
